<template>
  <el-row :gutter="10">
    <el-col :span="18" :offset="3">
      <p style="margin-bottom: 30px;line-height: 150%;">
        Sponge is a powerful Go development framework. Its core concept is to
        reverse-generate modular code by parsing JSON, SQL, or Protobuf files.
        The generated code can be flexibly and seamlessly assembled into various
        types of complete backend services (similar to the characteristics of
        sponge cells, where disassembled sponge cells can automatically
        recombine into a new sponge). Sponge provides an all-in-one solution for
        project development, covering code generation, development, testing, API
        documentation, and deployment, significantly enhancing development
        efficiency, reducing complexity, and enabling high-quality projects with
        a "low-code approach".
      </p>
      <p>
        Github Repository:
        <a href="https://github.com/go-dev-frame/sponge" target="_blank"
          >https://github.com/go-dev-frame/sponge</a
        >
      </p>
      <p>
        Development Documentation:
        <a href="https://go-sponge.com" target="_blank"
          >https://go-sponge.com</a
        >
      </p>
      <br />
      <p>
        <b>
          Sponge supports generating code for your project based on built-in
          templates and custom templates.
        </b>
      </p>
      <p>
        <b>1.</b> The code generation framework based on built-in templates is
        shown below.
      </p>
      <div class="home-image">
        <el-image :src="homeImageSrc"></el-image>
      </div>
      <br />
      <p>
        <b>2.</b> The code generation framework based on custom templates is
        shown below.
      </p>
      <div class="microservices-image">
        <el-image :src="homeTplImageSrc"></el-image>
      </div>
      <br />
      <p></p>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      homeImageSrc: require("@/assets/sponge-framework.png"),
      homeTplImageSrc: require("@/assets/template-framework.png"),
      mircoServiceFrameworkImageSrc: require("@/assets/microservices-framework.png"),
    };
  },
};
</script>

<style>
.home-image {
  margin: auto;
}

.microservices-image {
  width: 70%;
  margin: auto;
}

.key-word {
  /* background-color: #ddd; */
  color: #c7254e;
}
</style>



