<template>
  <el-row :gutter="10">
    <el-col :span="18" :offset="3">
      <p style="margin-bottom: 30px;line-height: 150%;">
        sponge 是一个强大的 Go 开发框架，其核心理念是通过解析 JSON、SQL 或
        Protobuf
        文件逆向生成模块化的代码，这些代码可以灵活、无缝地组合成多种类型的完整后端服务(类似海绵细胞的特性，打散的海绵细胞能自动重新组合成新的海绵)。sponge
        提供一站式项目开发解决方案，涵盖代码生成、开发、测试、API
        文档生成和部署，大幅提升开发效率，降低开发难度，实现以"低代码"方式构建高质量项目。
      </p>
      <p>
        github地址：<a
          href="https://github.com/go-dev-frame/sponge"
          target="_blank"
          >https://github.com/go-dev-frame/sponge</a
        >
      </p>
      <p>
        开发文档：<a href="https://go-sponge.com/zh-cn/" target="_blank"
          >https://go-sponge.com/zh-cn/</a
        >
      </p>
      <br />
      <p>
        <b>
          sponge 支持基于自带模板和自定义模板两种方式生成你的项目所需的代码。
        </b>
      </p>
      <p><b>1.</b> sponge 基于自带模板生成代码框架如下图所示：</p>
      <div class="home-image">
        <el-image :src="homeImageSrc"></el-image>
      </div>
      <br />
      <p><b>2.</b> sponge 基于自定义模板生成代码框架如下图所示：</p>
      <div class="microservices-image">
        <el-image :src="homeTplImageSrc"></el-image>
      </div>
      <br />
      <p></p>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      homeImageSrc: require("@/assets/sponge-framework.png"),
      homeTplImageSrc: require("@/assets/template-framework.png"),
      mircoServiceFrameworkImageSrc: require("@/assets/microservices-framework.png"),
    };
  },
};
</script>

<style>
.home-image {
  margin: auto;
}

.microservices-image {
  width: 70%;
  margin: auto;
}

.key-word {
  /* background-color: #ddd; */
  color: #c7254e;
}
</style>



